<template>
  <el-table :data="tableData" border style="width: 100%" highlight-current-row>
        <el-table-column type="index" label="" width="80" align="center" fixed></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.prop"
          :prop="item.prop"
          :label="item.label"
          show-overflow-tooltip
          :width="item.width"
          align="center"
        >
          <template slot-scope="scope">
            <!--  -->
            <span v-if="item.status==='flag'">
              <el-tag
                :type="scope.row.deviationResult === 0 ? 'success' :scope.row.deviationResult === 1?'primary':'danger' "
                disable-transitions
              >{{scope.row.deviationResult === 0 ? '' :scope.row.deviationResult === 1?'':''}}</el-tag>
            </span>
            <!--  -->
            <a
              v-else-if="item.status==='link'"
              class="link"
              @click.prevent="linkToinfo(scope.row)"
            >{{scope.row[item.prop]}}</a>
            <!--  -->
            <span v-else>{{scope.row[item.prop]}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" fixed="right">
          <template slot="header" slot-scope="scope">
            <span></span>
          </template>
          <template slot-scope="scope">
            <a href="#" class="link" @click.prevent="linkToinfo(scope.row)"></a>
          </template>
        </el-table-column>
      </el-table>
</template>
