      },
      {
        prop: "realQuantitySum",
        label: ""
      },

      {
        prop: "djly",
        label: ""
      },
      {
        prop: "completionTime",
        label: "",
        width: 200
      },
      {
        prop: "deviationResult",
        label: "",
        status: "flag"
      }
    ]
      }
    }
  }
</script>



